.home {
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    line-height: 1;
    height: calc(100vh - 5.5rem);
  }
  
  .home-content {
    margin-bottom: 100px;
  }
  
  .home-title {
    align-items: start;
  }
  
  .home-title h2 {
    padding-bottom: 1.5rem;
    font-size: 2rem;
    transform: translateY(0);
    animation: slideUp 0.8s ease forwards 0.5s;
  }
  
  .text-wrapper {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    transform: translateY(0);
    animation: slideUp 0.8s ease forwards 0.5s;
  }
  
  .text-wrapper p {
    font-size: 2.5rem;
    font-weight: 700;
  }
  
  .links {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 1.5rem;
    font-size: 1.5rem;
    font-weight: 700;
    transform: translateY(0);
    animation: slideUp 0.8s ease forwards 1.1s;
  }
  
  .link {
    position: relative;
    padding-bottom: 2px;
    transition: transform 0.3s ease-in-out;
  }
  
  .link a:hover {
    color: black;
    letter-spacing: 1.5px;
    transition: color 0.5s, letter-spacing 0.5s;
  }
  
  .link a::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    border: 2px solid black;
    transform: scaleX(0);
    transition: transform 0.6s cubic-bezier(0.19, 1, 0.22, 1);
    transform-origin: right center;
  }
  
  .link a:hover::after {
    transform: scaleX(1);
    transform-origin: left center;
    transition-duration: 0.4s;
  }
  
  .divider {
    font-size: 1.5rem;
    padding: 0 15px;
  }
  
  .scroll {
    position: relative;
    transform: translateY(0);
    animation: slideUp 0.8s ease forwards 1.3s;
  }
  
  .scroll a span {
    font-size: 1.5rem;
    font-weight: 700;
    position: absolute;
    bottom: -50px;
    width: 230px;
    height: 36px;
    color: black;
    background-color: #f9f9f9;
    border: 2px solid black;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 8px;
    transition: all 0.4s;
  }
  
  .scroll a span:hover {
    border: 2px solid black;
    transition: all 0.4s;
    color: #f9f9f9;
    background-color: black;
  }
  
  .scroll a span::before {
    content: '';
    position: absolute;
    top: 3px;
    left: 85%;
    width: 3px;
    height: 20px;
    background-color: #3498db;
    border-radius: 30%;
    animation: scroll ease 2s infinite;
    box-sizing: border-box;
  }
  
  .scroll a span:hover::before {
    background-color: #f9f9f9;
    transition: all 0.4s;
  }
  
  .circ {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .circ img {
    width: 450px;
    height: 450px;
    animation: animatedCircle 15s linear infinite;
  }
  
  .circle {
    width: 300px;
    height: 300px;
    position: absolute;
    background-color: #f9f9f9;
    border-radius: 50%;
    overflow: hidden;
  }
  
  .circle::before {
    content: '';
    position: absolute;
    inset: -10px 100px;
    background: linear-gradient(black, black);
    transition: 0.5s;
    animation: animatedCircle 15s linear infinite;
  }
  
  .circle::after {
    content: '';
    position: absolute;
    background-color: #f9f9f9;
    color: black;
    inset: 7px;
    border-radius: 50%;
    z-index: 1;
  }
  
  .content {
    position: absolute;
    inset: 30px;
    border: 5px solid black;
    z-index: 3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: column;
    transition: transform 0.3s ease-in-out;
  }
  
  .content img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: 0.5s;
    text-align: center;
    pointer-events: none;
    z-index: 3;
    animation: none;
  }
  
  .content a {
    position: relative;
    background-color: black;
    color: #f9f9f9;
    padding: 10px 10px;
    font-size: 1.5rem;
    font-weight: 600;
    border-radius: 15px;
    transition: 0.5s;
  }
  
  .content a:hover {
    letter-spacing: 1.5px;
  }
  
  .circle:hover::before {
    inset: -20px 0px;
  }
  
  .circle:hover img {
    opacity: 0;
  }
  
  @keyframes slideUp {
    0% {
      transform: translateY(0);
    }
  
    100% {
      transform: translateY(0);
    }
  }
  
  @keyframes fadeInOut {
    0% {
      opacity: 0;
      transform: scale(0.8);
    }
  
    30% {
      opacity: 1;
      transform: scale(0.8);
    }
  
    70% {
      opacity: 1;
      transform: scale(0.8);
    }
  
    100% {
      opacity: 0;
      transform: scale(0.8);
    }
  }
  
  @keyframes scroll {
    0% {
      transform: translateY();
    }
  
    100% {
      transform: translateY(10px);
    }
  }
  
  @keyframes animatedCircle {
    0% {
      transform: rotate(0);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  @media screen and (min-width: 769px) and (max-width: 890px) {
    .home-title h2 {
      font-size: 1.8rem;
      margin-bottom: 0.3rem;
    }
  
    .text-wrapper {
      font-size: 1.3rem;
    }
  
    .text-wrapper p {
      font-size: 2.2rem;
    }
  
    .links {
      font-size: 1.3rem;
      margin-top: 10px;
    }
  
    .link a::after {
      bottom: -3px;
    }
  
    .divider {
      font-size: 1.5rem;
    }
  
    .scroll a span {
      font-size: 1.5rem;
      bottom: -50px;
      width: 180px;
      height: 36px;
      padding: 8px;
    }
  
    .scroll a span::before {
      top: 3px;
      left: 80%;
      height: 15px;
    }
  
    .circle {
      width: 260px;
      height: 260px;
    }
  
    .circ img {
      width: 350px;
      height: 350px;
    }
  
    .content {
      inset: 25px;
    }
  
    .content a {
      font-size: 1.3rem;
      padding: 8px 8px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .home {
      flex-direction: column-reverse;
      justify-content: space-around;
      line-height: 0.8;
    }
  
    .home-content {
      margin-bottom: 30px;
    }
  
    .home-title h2 {
      font-size: 1.8rem;
      margin-bottom: 0.3rem;
    }
  
    .text-wrapper {
      font-size: 1.3rem;
    }
  
    .text-wrapper p {
      font-size: 2rem;
    }
  
    .links {
      font-size: 1.3rem;
      margin-top: 10px;
    }
  
    .link a::after {
      bottom: -3px;
    }
  
    .divider {
      font-size: 1.5rem;
    }
  
    .scroll a span {
      font-size: 1.5rem;
      bottom: -50px;
      width: 160px;
      height: 36px;
      padding: 8px;
    }
  
    .scroll a span::before {
      top: 3px;
      left: 80%;
      height: 15px;
    }
  
    .circle {
      width: 240px;
      height: 240px;
    }
  
    .circ img {
      width: 300px;
      height: 300px;
    }
  
    .content {
      inset: 20px;
    }
  
    .content a {
      font-size: 1.3rem;
      padding: 8px 8px;
    }
  }
  